/* basic styles */
.skeleton-wrapper {
  position: relative;
  overflow: hidden;
}
.product-wrapper {
  padding: 20px;
  margin: 20px 0;
  background: #E0E0E0;
  border-radius: 3px;
}
.skeleton {
  background: #ddd;
  border-radius: 4px;
}
.skeleton.title {
  width: 50%;
  height: 20px;
  margin: 0 auto;
}
.skeleton.thumbnail {
  width: 100%;
  height: 350px;
  margin-bottom: 20px;
}

.skeleton.thumbnail-small {
  width: 150px;
  height: 150px;
  margin-right: 20px;
  background: #BDBDBD;
}
/* animation effects */
.shimmer-wrapper {
  position: absolute;
  /* parent must be relative position & overflow hidden */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: loading 2.5s infinite;
}
.shimmer {
  width: 50%;
  height: 100%;
  background: rgba(255,255,255,0.2);
  transform: skewX(-20deg);
  box-shadow: 0 0 30px 30px rgba(255,255,255,0.2);
}

@keyframes loading {
  0% { transform: translateX(-150%) }
  50% { transform: translateX(-60%) }
  100% { transform: translateX(150%) }
}