* {
  box-sizing: border-box;
}

html {
  width: 100%;
}

body {
  margin: 0px;
  font-family: "Open Sans", sans-serif;
  width: 100%;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lora", serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

.navigation {
  z-index: 9;
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.5rem 0rem;
  background-color: #fff;
  color: black;
  padding: 30px;
}

.brand-name {
  font-family: "Water Brush", cursive;
  font-size: 1.5rem;
  text-decoration: none;
  color: black;
}

.navigation-menu {
  margin-left: auto;
}

.navigation-menu ul {
  display: flex;
  padding: 0;
}

.navigation-menu li {
  list-style-type: none;
  margin: 0 1rem;
}

.navigation-menu li a {
  display: block;
  width: 100%;
  cursor: pointer;
  color: #8a8a8a;
  transition: 0.5s ease-in-out;
}
.navigation-menu li a:hover {
  color: black;
}

.nav-link-wrapper {
  transition: 0.5s ease-in-out;
}
.nav-link-wrapper a {
  font-size: 0.9rem;
  color: #8a8a8a;
  text-decoration: none;
}
.nav-link-wrapper a:hover {
  color: black;
}
.nav-link-wrapper .nav-link-active {
  color: black;
}

.hamburger {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  background-color: #42454a;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  display: none;
}

.hamburger:hover {
  background-color: #4e5257;
}

@media screen and (max-width: 900px) {
  .hamburger {
    display: block;
  }

  .navigation-menu ul {
    flex-direction: column;
    position: absolute;
    top: 95px;
    left: 0;
    width: 100%;
    height: calc(100vh - 95px);
    background-color: white;
    border-top: 1px solid black;
    display: none;
  }

  .navigation-menu li {
    text-align: center;
    margin: 0;
  }

  .navigation-menu li a {
    color: black;
    width: 100%;
    padding: 1.5rem 0;
  }

  .navigation-menu li:hover {
    background-color: #eee;
  }

  .navigation-menu.expanded ul {
    display: block;
  }
}
.one-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
}

.two-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr;
}

textarea {
  border: 1px solid #66b2b2;
  background-color: transparent;
  font-size: 1rem;
  height: 100px;
  outline: none;
  padding: 10px;
}

.nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
}
.nav-wrapper .left-side .brand {
  font-family: "Water Brush", cursive;
  font-size: 1.5rem;
  text-decoration: none;
  color: black;
}
.nav-wrapper .right-side {
  display: flex;
}
.nav-wrapper .right-side a {
  margin-left: 15px;
  cursor: pointer;
  color: #66b2b2;
  transition: 0.5s ease-in-out;
}
.nav-wrapper .right-side a:hover {
  color: #4d9898;
}
.nav-wrapper .nav-link-wrapper {
  height: 18px;
  transition: 0.5s ease-in-out;
  margin-right: 20px;
  font-size: 0.9rem;
}
.nav-wrapper .nav-link-wrapper a {
  color: #8a8a8a;
  text-decoration: none;
}
.nav-wrapper .nav-link-wrapper a:hover {
  color: black;
}
.nav-wrapper .nav-link-wrapper .nav-link-active {
  color: black;
}

.portfolio-detail-wrapper {
  width: 100%;
}
.portfolio-detail-wrapper .portfolio-detail-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 200px;
  align-items: center;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.1);
}
.portfolio-detail-wrapper .portfolio-detail-header .description {
  width: 700px;
  font-size: 2em;
  margin-bottom: 50px;
}

.portfolio-items-wrapper {
  overflow-x: hidden;
  margin: 0 5vw 100px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}
.portfolio-items-wrapper a {
  text-decoration: none;
  color: #42454a;
  transition: 0.4s ease-in-out;
}
.portfolio-items-wrapper a:hover {
  color: #8a8a8a;
}
.portfolio-items-wrapper .portfolio-item-wrapper {
  text-align: center;
  background-color: black;
}
.portfolio-items-wrapper .portfolio-item-wrapper .portfolio-img-background {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 350px;
  width: 100%;
  mix-blend-mode: luminosity;
}
.portfolio-items-wrapper .portfolio-item-wrapper .portfolio-img-background:hover {
  mix-blend-mode: unset;
}
.portfolio-items-wrapper .portfolio-item-wrapper .collection-title {
  background-color: white;
}
.portfolio-items-wrapper .portfolio-item-wrapper .collection-title {
  font-size: 0.9rem;
  font-weight: 400;
  margin-top: 0;
  padding-top: 20px;
}

.image-blur {
  transition: 1s;
  mix-blend-mode: unset !important;
}

@media (min-width: 576px) {
  .portfolio-items-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 992px) {
  .portfolio-items-wrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1200px) {
  .portfolio-items-wrapper {
    margin: 0 10vw 100px;
  }
}
.manager-wrapper {
  display: grid;
  grid-template-columns: 3fr 1fr;
}
.manager-wrapper .left-column {
  background-color: #f6f6f6;
}
.manager-wrapper .right-column {
  background-color: #42454a;
}

.portfolio-form-wrapper {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
  padding: 42px;
}
.portfolio-form-wrapper input {
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #66b2b2;
  background-color: transparent;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}
.portfolio-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #4d9898;
}

.image-uploaders {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb {
  padding: 21px;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .portfolio-thumb-img img {
  width: 100%;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .title {
  color: #f6f6f6;
  font-size: 1.5em;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .delete-icon {
  cursor: pointer;
  color: #f6f6f6;
  font-size: 1.5em;
  transition: 0.5s ease-in-out;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .delete-icon:hover {
  color: #922a2a;
}

.auth-page-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  height: calc(100vh - 95px);
}
.auth-page-wrapper .left-column {
  background-size: cover;
  min-height: 200px;
}
.auth-page-wrapper .right-column {
  padding: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f6f6f6;
}
.auth-page-wrapper .right-column .auth-form-wrapper {
  display: grid;
  grid-gap: 21px;
  grid-gap: 21px;
  padding: 42px;
}
.auth-page-wrapper .right-column .auth-form-wrapper input {
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #66b2b2;
  background-color: transparent;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}
.auth-page-wrapper .right-column .auth-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #4d9898;
}
.auth-page-wrapper .right-column .auth-form-wrapper .form-group {
  display: flex;
}
.auth-page-wrapper .right-column .auth-form-wrapper .form-group svg {
  font-size: 2em;
  color: #66b2b2;
  margin-right: 15px;
}

@media (min-width: 768px) {
  .auth-page-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
.btn {
  cursor: pointer;
  height: 42px;
  font-size: 1em;
  font-weight: 500;
  border: 1px solid transparent;
  transition: all 0.5s ease-in-out;
  background-color: #66b2b2;
  color: white;
}
.btn:active, .btn:focus {
  outline: none;
}
.btn:hover {
  background-color: #4d9898;
}

.content-page-wrapper {
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr;
  height: calc(100vh - 95px);
}
.content-page-wrapper .right-column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 100px;
}
.content-page-wrapper .right-column .contact-bullet-points {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 21px;
}
.content-page-wrapper .right-column .contact-bullet-points .bullet-point-group {
  display: flex;
  align-items: center;
}
.content-page-wrapper .right-column .contact-bullet-points .bullet-point-group .icon {
  font-size: 2em;
  color: #66b2b2;
}
.content-page-wrapper .right-column .contact-bullet-points .bullet-point-group .text {
  font-size: 1.5em;
  font-weight: 700;
  margin-left: 21px;
}

@media (min-width: 768px) {
  .content-page-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767.98px) {
  .content-page-wrapper .right-column {
    padding: 30px;
  }
  .content-page-wrapper .right-column .contact-bullet-points .bullet-point-group .icon,
.content-page-wrapper .right-column .contact-bullet-points .bullet-point-group .text {
    font-size: 1em;
  }
}
.marina-store {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 95px);
  margin: 0 auto;
}

.marina-store > * {
  padding: 2rem;
}

.marina-store > *:first-child {
  flex-basis: 60%;
  background: #f6f6f6;
}

.marina-store > *:nth-child(2) {
  flex-basis: 40%;
  min-width: 300px;
  background: #f1f1f1;
}

ul.order li {
  border-bottom: 1px solid #000;
  padding: 2rem 0;
  display: flex;
  font-size: 1rem;
  justify-content: space-between;
  align-items: center;
}

ul.order li:hover button {
  display: inline;
  cursor: pointer;
}

ul.order li button {
  border: 0;
  display: inline;
  line-height: 1;
  width: 30px;
}

ul.order li button:hover {
  text-decoration: underline;
}

ul.order li .price {
  font-size: 1.2rem;
}

ul.order li span.count {
  position: relative;
  overflow: hidden;
  float: left;
}

ul.order li span.count span {
  display: inline-block;
}

.total {
  padding: 2rem 0;
  font-size: 1rem;
  border-bottom: 3px solid #000;
  border-top: 1px #000;
}

.total strong {
  float: right;
}

.order-title {
  text-align: center;
}

/* shop products list */
.list-of-product {
  border-top: 2px solid #000;
  border-bottom: 1px solid #000;
  padding-top: 5px;
  margin-top: 2rem;
}

.menu-product {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #000;
  padding-bottom: 2rem;
  padding-top: 2rem;
  margin-bottom: 5px;
  clear: both;
  overflow: hidden;
}
.menu-product .product-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.menu-product .product-wrapper img {
  width: 100%;
  margin-right: 20px;
  margin-bottom: 20px;
}
.menu-product .right-stuff {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
}
.menu-product .right-stuff .title-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.3rem;
}
.menu-product .right-stuff .title-wrapper .price {
  color: #8a8a8a;
  font-size: 0.9rem;
}
.menu-product .right-stuff p {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1rem;
}
.menu-product .right-stuff .btn {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-start;
  width: 100%;
}
.menu-product:first-child {
  border-top: 1px solid #000;
}

.cart-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.inventory-form-wrapper {
  padding: 42px;
  height: calc(100vh - 95px);
}
.inventory-form-wrapper input {
  color: #42454a;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #66b2b2;
  background-color: transparent;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
}
.inventory-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #4d9898;
}
.inventory-form-wrapper textarea {
  background: #fff;
  width: 100%;
  margin-bottom: 20px;
}

.add-product-btn {
  margin-top: 20px;
  width: 100%;
}

.checkout-btn {
  margin-top: 50px;
}

@media (min-width: 768px) {
  .menu-product .product-wrapper {
    flex-direction: row;
  }
  .menu-product .product-wrapper img {
    width: 170px;
  }
  .menu-product .product-wrapper .right-stuff .title-wrapper .price {
    color: #8a8a8a;
    font-size: 1rem;
  }
  .menu-product .product-wrapper .btn {
    width: 170px;
  }

  .marina-store {
    flex-direction: row;
    height: calc(100vh - 95px);
  }

  .marina-store > *:first-child {
    overflow: scroll;
  }

  ul.order li button {
    border: 0;
    display: none;
    line-height: 1;
    padding: 0;
  }
}